.form-select {
  width: 300px;
  height: 50px;
}
.slector-input{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 400px;
}
.facebook-btn{
  background:none ;
  border: none;
}
.visblePasswordIcon{
  font-size: 30px;
  position: absolute;
  right: 5px;
  top: 12px;
  color: gray;
  cursor: pointer;
}
select:required:invalid {
  color: gray;
}
option[value=""][disabled] {
  display: none;
}
option {
  color: black;
}

select {
  padding: 2px 10px;
  border: 1px solid #979997;

  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;

  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  border-radius: 4px;
  cursor: pointer;
 
}