$primary: #00b4ff; //primary color
$secondary: #2683ff; // secondary color

.mainHeader {
  // box-shadow: 0px 3px 19px rgba(0, 0, 0, 0.25);
  position: sticky;
  top: 0;
  transition: 0.2s;
  z-index: 999;
  max-width: 1400px;
  margin: 0 auto;
}

@media (max-width: 700px) {
  .header-box {
    width: 360px !important;
  }
}
.Header {
  background-color: #ffffff !important;
  height: 96px;
  justify-content: center;
  box-shadow: none !important;
  @media (max-width: 397px) {
    height: 120px;
  }
  .headerlogo {
    height: 56px;
  }
  .link1 {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: #545454;
    cursor: pointer;
    text-decoration: none;
    line-height: 28px;
  }
  .link1:visited {
    text-decoration: none;
  }
  .bothbtndiv {
    .headerLogbtn {
      width: 100px;
      height: 40px;
      border: 1px solid $primary;
      border-radius: 13px;
      font-family: "Inter";
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      background: #ffffff;
      color: $primary;
      margin-right: 10px;
      padding: 6px 10px;
      text-decoration: none;
      cursor: pointer;
      @media (max-width: 397px) {
        width: 50px;
        height: 20px;
        text-align: center;
      }
    }
    .headerSignbtn {
      width: 100px;
      padding: 6px 10px;
      text-decoration: none;
      height: 40px;
      border: 1px solid $primary;
      border-radius: 13px;
      background: $primary;
      font-family: "Inter";
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      cursor: pointer;

      color: #ffffff;
      @media (max-width: 397px) {
        margin-top: 10px;
      }
      @media (max-width: 480px) {
        display: none;
      }
    }
    @media (max-width: 397px) {
      display: flex;
      flex-direction: column;
    }
  }
  .css-14bc5je {
    @media (max-width: 480px) {
      justify-content: right;
    }
  }
  .css-19r6kue-MuiContainer-root {
    padding: 0 20px 0 20px;
    @media (min-width: 1536px) {
      max-width: none !important;
    }
  }
}
#menu-appbar1 {
  display: flex !important;
  flex-direction: column !important;
  .link1 {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: #545454;
    text-decoration: none;
    line-height: 28px;
    cursor: pointer;
  }
  .css-6hp17o-MuiList-root-MuiMenu-list,
  .css-r8u8y9 {
    display: flex;
    flex-direction: column;
    width: 120px;
    height: 100px;
    justify-content: space-around;
    padding-left: 10px;
  }
}
.css-1eude84 {
  @media (max-width: 690px) {
    display: none !important;
  }
}
.css-14bc5je {
  @media (max-width: 600px) {
    display: flex !important;
  }
}

.topHeaderMain {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.topLeftIconMenu {
  display: flex;
  align-items: center;
}

.topImgHeaderRight {
  display: flex;
  align-items: center;
  flex-direction: column;
}
